import { Component, OnInit } from '@angular/core';
import { TrafficUrlService } from 'src/app/core/service/traffic-url/traffic-url.service';

@Component({
  selector: 'app-change-ip',
  templateUrl: './change-ip.component.html',
  styleUrls: ['./change-ip.component.scss']
})
export class ChangeIPComponent implements OnInit {

  ipaddress = localStorage.getItem('ip');
  message="";

  constructor(private mockUpService: TrafficUrlService) { 
    if(this.ipaddress == null){
      this.ipaddress ="127.0.0.1";
    }
  }

  ngOnInit() {
  }
  

   onSubmit(ipAddress: string) {
    if (confirm("This action will be reload this page. Continue?")) {
    console.log(ipAddress);
    if(localStorage.getItem('ip') == null){
      localStorage.setItem('ip',ipAddress);
      console.log("case 1: "+localStorage.getItem('ip'));
    }
    
    else {
      localStorage.removeItem('ip');
      localStorage.setItem('ip',ipAddress);
      console.log("case 2: "+localStorage.getItem('ip'));
    }

    window.location.reload();
  }
  }
}
